// aqu irn las llamadas api al servidor
import Axios from 'axios';
// otra manera sera con el mtodo de js el fetch, ms sencillo con axios

const url = `${process.env.BASE_API_URL}/login`;

// get
// post
// put
// delete

export const isValidLogin = (login) =>
  Axios.post(url, login).then((response) => {
    return response.data;
  });
